@import "../../scss/variables";

@-webkit-keyframes mover {
	0% {
		transform: translateY(0);
	}
	100% {
		transform: translateY(-20px);
	}
}
@keyframes mover {
	0% {
		transform: translateY(0);
	}
	100% {
		transform: translateY(-20px);
	}
}
.register {
	box-shadow: 0px 5px 25px rgba(39, 55, 76, 0.2);
	.register-form {
		padding: 10%;
		margin-top: 10%;
		width: 100%;
	}
	.nav-tabs {
		margin-top: 3%;
		border: none;
		background: #ebf2fb;
		border-radius: 1.5rem;
		float: right;
		margin-right: 2%;
		display: -webkit-inline-box;
		.nav-link {
			height: 34px;
			font-weight: 600;
			color: #374254;
			border-radius: 1.5rem;
			padding: 1rem 2rem;
			left: 0;
			right: 0;
			margin: 0 auto;
			text-align: center;
			&:hover {
				border: none;
			}
		}
		.nav-link.active {
			border-radius: 1.5rem;
			width: 100%;
			background: transparent;
		}
	}
}
.register-left {
	text-align: center;
	color: #fff;
	margin-top: 4%;
	input {
		border: none;
		border-radius: 1.5rem;
		padding: 2%;
		width: 60%;
		background: #f8f9fa;
		font-weight: bold;
		color: #383d41;
		margin-top: 30%;
		margin-bottom: 3%;
		cursor: pointer;
	}
	img {
		margin-top: 15%;
		margin-bottom: 5%;
		width: 25%;
		-webkit-animation: mover 2s infinite alternate;
		animation: mover 1s infinite alternate;
	}
	p {
		font-weight: lighter;
		padding: 12%;
		margin-top: -9%;
	}
}
.register-right {
	background: #fff;
	border-top-left-radius: 10% 50%;
	border-bottom-left-radius: 10% 50%;
}
.btnRegister {
	float: right;
	margin-top: 10%;
	border: none;
	border-radius: 1.5rem;
	padding: 2%;
	color: #fff;
	font-weight: 600;
	width: 50%;
	cursor: pointer;
}
.register-heading {
	text-align: center;
	margin-top: 8%;
	margin-bottom: -15%;
	color: #495057;
}
@media (max-width:767px) {
	.register-right {
		border-top-left-radius: 0;
		border-bottom-left-radius: 0;
	}
	.register {
		.nav-tabs {
			width: 100%;
			margin: 2rem auto !important;
			text-align: center;
		}
	}
	.register-left {
		input {
			margin-top: 0;
			margin-bottom: 10%;
		}
	}
	.btnRegister {
		float: none !important;
		text-align: center;
	}
}
